// Параметры контейнера и блоков
$containerPadding: 15;
$maxWidth: 1440;
$maxWidthContainerCount: 1220;
$maxWidthContainer: $maxWidthContainerCount + ($containerPadding * 2);

// Переменные основных цветов
$accent: #393c42;
$text: #1eab89;
$white: #e4e8ef;
$dark: #505050;
$shadow: $white 4px 4px 24px 0px;

// Переменные геометрии
$radius: rem(22.5);

// Параметры основного семейства шрифта
$base-font: "Times-New-Roman", sans-serif;
$base-font-weight: 500;
$base-font-size: rem(16);
$base-font-color: $accent;

// Карта базовых брейкпоинтов
$breakpoints: (
  "xxl": 1400px,
  "xl": 1200px,
  "lg": 992px,
  "md": 768px,
  "sm": 576px,
  "xs": 320px,
);

// Карта для определения максимальной ширины контейнера на брейкпоинте
$container-widths: (
  "xxl": $maxWidthContainer + px,
  "xl": 1140px,
  "lg": 960px,
  "md": 720px,
  "sm": 540px,
  "xs": 100%,
);
